<template>
    <div class="list_box">
        <div class="searchBox">
            <h3><span @click='goLast' class="icon-left"></span>
                <p>选择开通</p>
            </h3>
        </div>


        <div class="wallet_btn chooseone">
            <yd-button @click.native="linkTo('/one')" bgcolor="#5e1515" color="#fff" size="large" type="danger">开通下级员工</yd-button>
        </div>
        <div class="wallet_btn choosetwo " v-show="level=='partner'">
            <yd-button @click.native="linkTo('/shop')" bgcolor="#5e1515" color="#fff" size="large" type="danger">开通店铺</yd-button>
        </div>

    </div>
</template>
<script>
    import ajax from "../../ajax/ajax.js";
    import dataURL from "../../server/dataUrl.js";
    export default {
        data() {
            return {
                level: ""
            }
        },
        methods: {
            goLast() {
                this.$router.go(-1);
            },
            linkTo(adr) {
                this.$router.push({
                    path: adr
                });
            },
            async getUserinfo() {
                let res = await ajax.Get(dataURL.main.userInfo);
                console.log(res);
                this.level = res.level;
            }
        },
        created() {
            this.getUserinfo();
        }
    }
</script>


